.card {
    &.card--news {
        .card__anchor {
            background-color: $color-purewhite;

            &,
            &:focus,
            &:active,
            &:visited {
                border: none;
            }

            &:hover {
                @include media('hover') {
                    border: none;
                    background-color: none;
                }
            }

            .card--news_f_label--news + .card__details__text {
                @include media('>mobile', '<small') {
                    padding-top: $measure--medium3;
                }
            }

            .card--news__label--news {
                @include smalltype();
                margin-top: 0;

                color: $color-gray7;
                font-style: italic;
            }

            .card--news__label--news + .card__details__text {
                @include media('>=mobile', '<tablet') {
                    margin-top: $measure--medium1;
                }
            }

            .card__details__text {
                padding-bottom: $measure--basic;
                padding-top: $heading-margin;
            }

            .card__type {
                margin-top: 0;
                @include smalltype();
                color: $color-gray7;
            }

            &:after {
                content: unset;
                display: none;
            }
        }

        &.card--landscape {
            @include landscape-card();
        }

        @include media('>=mobile', '<tablet') {
            @include landscape-card();
        }
    }
}
